const dictionary = {
  "Organic Chemistry Reaction Prediction": {
    "vi": "D on Phn ng Ho hc Hu c",
  },
  "SMILES inputs": {
    "vi": "Nhp d liu dng SMILES"
  },
  "Reactants": {
    "vi": "Cht phn ng",
  },
  "Known reactants": {
    "vi": "Cht tham gia phn ng",
  },
  "Products": {
    "vi": "Sn phm",
  },
  "Predicted products": {
    "vi": "Sn phm d on",
  },
  "Structure diagram": {
    "vi": "Cu trc 2D",
  },
  "3D structure": {
    "vi": "Cu trc 3D",
  },
  "Structure of": {
    "vi": "Cu trc ca",
  },
  "Predict": {
    "vi": "D on",
  },
  "Result": {
    "vi": "Kt qu",
  },
  "Formula": {
    "vi": "Cng thc",
  }
};

export const translate = (keyword, language) => {
  if (!(keyword in dictionary)) return keyword;
  const keyword_translated = dictionary[keyword];
  if (!(language in keyword_translated)) return keyword;
  return keyword_translated[language];
};
